import MasterPage from '../components/Master';
import Precio from '../components/Precio'
import Noticias from '../components/Noticias'
import fetch from 'isomorphic-unfetch';//tuvimos que instalarlo
import Eventos from '../components/Eventos';

const Index = (props) =>(
  <MasterPage>
      <div className="row">
          <div className="col-12">
              <h2>Precio de Bitcoin</h2>
              {console.log(props)} {/*con getInitialProps obtenemos props*/}
              <Precio
                    precio={props.precioBitcoin}
                  />
          </div>


      <div className="col-md-8">
          <h2 className="my-5">Noticias sobre bitcoin</h2>

          <Noticias
              noticias={props.noticias}
          />
      </div>

      <div className="col-md-4">
          <h2 className="my-5">Eventos de bitcoin</h2>
          {console.log("proxima linea eventos")}

          <Eventos
              eventos={props.eventos}
          />
      </div>
      </div>

  </MasterPage>

);

Index.getInitialProps = async () => {//en next no hay componentdidmount, esto lo reemplaza en next. Una vez que carga hace la consulta a la API
        const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
        //TODO colocar noticias en un try-catch, porque esta mandando a paginas inexistentes
        const noticias= await fetch('https://newsapi.org/v2/everything?q=bitcoin&' +
            'sortBy=publishedAt&apiKey=e64980643f32422e88f936bd0d65dde5&language=es');
         const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=S5XAABALGPTL2ALVY5SI');

        const resPrecio = await precio.json();
        const resNoticias = await noticias.json();
        const resEventos = await eventos.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD, //las propiedades de resprecio las averiguo con console.log.resPrecio
        noticias:resNoticias.articles,
        eventos:resEventos.events

    }
}
export default Index;